.App {
  text-align: center;
}

/* LOGO */

.App-logoWrapper {
  margin: 25px 0 25px 0;
  text-align: center;
}

.App-logo {
  text-align: center;
  width: 175px;
}

/* HEADER */

.App-header {
  border-top: 12px solid #d8f71c;
  font-size: 28px;
  padding: 15px;
  margin: 10px 0 0 0;
  background-color: #005f89;
  color: white;
}

/* CONTACT */

.App-mail {
  font-size: 28px;
  padding: 20px;
  background-color: #222;
  color: white;

}

.App-mail__start {
  font-size: 28px;
  font-weight: bold;

}

.App-mail__email {
  font-size: 22px;
  text-decoration: underline;
  -webkit-text-decoration-style: solid;
          text-decoration-style: solid;
  -webkit-text-decoration-color: #d9f71a;
          text-decoration-color: #d9f71a;
}

/* CLIENTS */

.App-clients {
  font-size: 28px;
  padding: 20px;
}

.App-clients__start {
  font-weight: bold;
}

.App-clientLogoWrapper {
  margin: 20px 0 20px 0;
  text-align: center;
}

.App-clientLogo {
  text-align: center;
  width: 110px;
  vertical-align: middle;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  padding: 5px 10px;
}

/* FOOTER */

.App-socialLogo {
  text-align: center;
  color: white;
  font-size: 30px;
  padding: 0 5px;
}

.App-footer {
  text-align: center;
  padding: 15px;
  margin: 0;
  vertical-align: middle;
  background-color: #222;
  color: white;
  font-size: small;
}

.App-footer_text {
  margin: 10px;
}



